*{
	margin:0;
	padding:0;
}
@keyframes ani{
    /* img_width * vew_height / img_height = img_view_width, is where to repeat anime*/
    0%{background-position: calc(512*100vh/256) 0;} 
	100%{background-position: 0 0;}
}
