.panel {
    position: absolute;
    width: 100vw;
    height: 100vh;
    overflow: hidden;

    text-align: center;
}

.centerBar {
    margin-top: 20px;
}

.sideBar {
    text-align: right;
}

.sideBar ul {
    margin: 30px;
    list-style-type: none;
    list-style-type: none;
}

.sideBar .pixel-button, .sideBar .pixel-box {
    padding: 7px;
}

.attribute {
    text-align: left;
}

.menu-panel {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: scroll;
    text-align: center;
}

.chickenPageClass ul {
    list-style-type: none;
}

.chickenPageClass li {
    margin: 5px;
}