
:root {
    --btn-inside: white;
    --btn-border: black;
    
    --btn-inside-hover: #dcdcdc;
    --btn-border-hover: #3b3b3b;

    --btn-inside-active: #b9b9b9;
    --btn-border-active: #2f2f2f;
}

/* default pixel-button */
.pixel-button, .pixel-box {
    background-color: var(--btn-inside);
    border-color: var(--btn-border);
    border-style: solid;
    border-width: 6px 0;
    color: var(--btn-border);
    display: inline-block;
    margin-left: 12px;
    padding: 15px;
    position: relative;
    text-decoration: none;
    /*text-transform: uppercase;*/
    font-size: 18px;
    font-family: defaultFont;
}

.pixel-button img {
    height: 40px;
    vertical-align: middle;
}


.pixel-button::after, .pixel-box::after {
    background-color: var(--btn-inside);
    box-shadow: 6px 0 0 var(--btn-border), 0 -6px var(--btn-border), 0 6px var(--btn-border);
    content: '';
    display: block;
    height: calc(100% - 12px);
    position: absolute;
    right: -6px;
    top: 6px;
    width: 6px;
}

.pixel-button::before, .pixel-box::before {
    background-color: var(--btn-inside);
    box-shadow: -6px 0 0 var(--btn-border), 0 -6px var(--btn-border), 0 6px var(--btn-border);
    content: '';
    display: block;
    height: calc(100% - 12px);
    position: absolute;
    left: -6px;
    top: 6px;
    width: 6px;
}

.pixel-button:hover {
    background-color: var(--btn-inside-hover);
    border-color: var(--btn-border-hover);
    color: var(--btn-border-hover);
}
.pixel-button:hover::before,
.pixel-button:hover::after {
    background-color: var(--btn-inside-hover);
}
.pixel-button:hover::before {
    box-shadow: -6px 0 0 var(--btn-border-hover), 0 -6px var(--btn-border-hover), 0 6px var(--btn-border-hover);
}
.pixel-button:hover::after {
    box-shadow: 6px 0 0 var(--btn-border-hover), 0 -6px var(--btn-border-hover), 0 6px var(--btn-border-hover);
}

.pixel-button:active {
    background-color: var(--btn-inside-active);
    border-color: var(--btn-border-active);
    color: var(--btn-border-active);
}
.pixel-button:active::before,
.pixel-button:active::after {
    background-color: var(--btn-inside-active);
}
.pixel-button:active::before {
    box-shadow: -6px 0 0 var(--btn-border-active), 0 -6px var(--btn-border-active), 0 6px var(--btn-border-active);
}
.pixel-button:active::after {
    box-shadow: 6px 0 0 var(--btn-border-active), 0 -6px var(--btn-border-active), 0 6px var(--btn-border-active);
}


/* override var bellow pixel-button */
.connect-wallet-button {
    --btn-inside: rgb(32, 129, 226);
}

.mint-nft-button {
    --btn-inside: orange;
}
 