.landingPage #onBtnImg {
    margin-bottom: -16px;
}

.landingPage p {
    font-size: 25px;
    margin: auto;
    max-width: 600px;
}

.landingPage .pixel-button {
    margin-bottom: 10px;
}

.landingPage .landingPanelTop {
    position: absolute;
    left: 0;
    right: 0;
    top: 15vh;
    text-align: center;
    z-index: 1;
    padding: 10px;
}

@media only screen and (max-width: 600px) {
    .landingPage .landingPanelTop {
        top: 5vh;
    }
}

@media only screen and (max-width: 600px) {
    .landingPage .socialIconDiv {
        visibility: hidden;
    }    
}

.landingPage .socialIconDiv {
    position: absolute;
    right: 20px;
    top: 10px;
}

.landingPage .socialIcon img{
    height: 48px;
    margin: 10px;
}

.landingPage .socialIcon img:hover {
    opacity: 0.7;
}

.landingPage .midText {
    transform: translateY(-50vh); /* This is to move midText up to ReflectionAnime */
}

.landingPage #title {

    font-size: 70px;
    /*padding-top: 30vh;*/
    text-align: center;
    /*font-weight: 100;*/
    /*font-weight:bolder;*/
}

.landingPage h2 {
    font-size: 30px;
}

.landingPage h3 {
    font-size: 25px;
}


.landingPage .landingPanelMid {
    position: absolute;
    left: 0;
    right: 0;
    top: 140vh;
    text-align: center;
    z-index: 1;
    padding: 20px;
    overflow: hidden;
}