@keyframes ani {

    /* img_width * vew_height / img_height = img_view_width, is where to repeat anime*/
    0% {
        background-position: calc(512*100vh/256) 0;
    }

    100% {
        background-position: 0 0;
    }
}

.skyDiv1 {
    background: url("/src/assets/img/Field/Field Layer 01 cloud upside down.png") repeat 0 / auto 100%;
    /*height: 100%; //1280px; /* this value is not important, just not zero */
    background-size: auto 100vh;

    width: 100%;
    animation: ani 256s linear infinite;
    margin-bottom: -100vh;
    /* this is to move Footer up */
}